import React from "react";
import { Container, Row } from "react-bootstrap";
import TalkToUsNew from "./talk-to-us-new";

const CtaTwo = (props) => {
  const { db, dynamicHeaderText } = props;
  const defaultHeaderText =
    "Justtry is your trusted POC development company. <br/>Contact us for groundbreaking solutions!";
  return (
    <>
        <Row className="cta_two_wrapper">
          <div className="enterprise_block_cta_section">
            <div className="enterprise_block_cta_content">
              <h2
                dangerouslySetInnerHTML={{
                  __html: dynamicHeaderText || defaultHeaderText,
                }}
              ></h2>
              <div className="enterprise_block_btn_wrapper">
                <button type="submit" className="blockchain_cta_btn">
                  <TalkToUsNew label="Talk to us" db={db} />
                </button>
              </div>
            </div>
            <div className="enteprise_block_cta"></div>
          </div>
        </Row>
    </>
  );
};

export default CtaTwo;
